$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: "Source Sans 3", serif;
$bodyWidth: 1024px;

// global settings
body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  margin-left: auto;
  margin-right: auto;
}

//header
.header {
display: flex;
flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  width: 1024px; 
}

.authorname {
width: 310px;
}

.topnav {
display: flex;
  float: right;
margin-left: 25%;
margin-top: 20px;
width: 725px;

  .nav-list ul {
    list-style: none;

	}
	
  .nav-list a {
    padding: 10px;
	font-size: 18px;	
    }
 }
 
// front content
.flexbox {
  display: flex;
  align: center;
  flex-direction: row;

  height: 780px;
  background-image: url('/assets/images/oauj1.jpg');
}

.flexbox .rightbox {
display: flex;
margin-top: 250px;
margin-left: 35%;
align: center;
height: 160px;
border-bottom: solid 2px #e1e1e1;

}

.rightbox p{
color: #FFFFFF;
line-height: 1.25;
font-size: 28px;
}

.footer {
margin-left: 35%;
width: 1024px;
justify-content: center;
color: #686D76;
font-size: 13px;
}